.stars {
  display: flex;

  &__star {
    width: 16px;
    height: 16px;
    background-image: url('../images/star.svg');
    background-repeat: no-repeat;
    background-position: center;
    margin-right: 4px;

    &:last-child {
      margin-right: 0;
    }
  }
}

@for $i from 1 through 5 {
  .stars--#{$i} :nth-child(-n + #{$i}) {
    background-image: url('../images/star-active.svg');
  }
}
